}

nav.responsive-toolbar{background:#333333;height:60px;width:100%;position:fixed;box-shadow:2px 0px 2px #ccc}nav.responsive-toolbar ~ *{padding-top:60px}nav.responsive-toolbar ul{background:#333333;display:flex;-webkit-margin-before:0;margin-block-start:0;-webkit-margin-after:0;margin-block-end:0;-webkit-padding-start:0;padding-inline-start:0;height:100%;align-items:center;box-shadow:2px 2px 2px #ccc}nav.responsive-toolbar ul figure{-webkit-margin-before:0;margin-block-start:0;-webkit-margin-after:0;margin-block-end:0;-webkit-margin-start:10px;margin-inline-start:10px;-webkit-margin-end:0;margin-inline-end:0;cursor:pointer}nav.responsive-toolbar ul li{list-style-type:none;padding:10px 20px}nav.responsive-toolbar ul li .active{background:#999}nav.responsive-toolbar ul a{color:white;text-decoration:none;display:flex;flex-direction:row-reverse;align-items:center}nav.responsive-toolbar ul i{margin-right:5px;font-size:20px}@media screen and (max-width: 759px){nav.responsive-toolbar ul{height:100%;width:300px;left:-240px;position:fixed;top:60px;flex-direction:column;transition:300ms ease all}nav.responsive-toolbar ul.active{left:0px}nav.responsive-toolbar ul figure{-webkit-margin-start:2px;margin-inline-start:2px;position:fixed;left:9px;top:10px}nav.responsive-toolbar ul li{width:100%;padding-left:0;padding-right:0}nav.responsive-toolbar ul a{flex-direction:row;justify-content:space-between;margin-left:20px;margin-right:16px}}figure{-webkit-margin-before:0;margin-block-start:0;-webkit-margin-after:0;margin-block-end:0;-webkit-margin-start:10px;margin-inline-start:10px;-webkit-margin-end:0;margin-inline-end:0}body{margin:0}

.canvas {
    width:90vw;
    display: flex;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;
    flex-direction: column;
    position: relative;
}

.canvas__mobile {
    width:75vw;
    display: flex;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;
    flex-direction: column;
    position: relative;
}

.section__mobile {
    width: 75vw;
    height: 70vh;
    overflow: hidden;
    border-style: solid;
    box-sizing: border-box;
}

.canvas__top {
    width:90vw;
    height: 30vh;
    display: flex;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;
    flex-direction: row;
}

.canvas__middle {
    height: 70vh;
	width:90vw;
    display: flex;
    flex-direction: row;
}

.canvas__bottom {
    height: 30vh;
	width:90vw;
    display: flex;
    flex-direction: row;
}

.minor {
    width: 90%;
}

.col__long {
    width: 18vw;
    height: 70vh;
    overflow: hidden;
    border-style: solid;
    box-sizing: border-box;
}

.col__long__nohover {
    width: 18vw;
    height: 70vh;
    overflow: hidden;
    border-style: solid;
    box-sizing: border-box;
}
.col__long:hover {
    background-color: lightgray;
    cursor: pointer;
}

.col__short {
    width: 18vw;
    height: 35vh;
    overflow: hidden;
    border-style: solid;
    box-sizing: border-box;
}
.col__short__nohover {
    width: 18vw;
    height: 35vh;
    overflow: hidden;
    border-style: solid;
    box-sizing: border-box;
}

.col__short:hover {
    background-color: lightgray;
    cursor: pointer;
}


.col__short__short {
    width: 18vw;
    height: 30vh;
    overflow: hidden;
    border-style: solid;
    box-sizing: border-box;
}
.col__short__short__nohover {
    width: 18vw;
    height: 30vh;
    overflow: hidden;
    border-style: solid;
    box-sizing: border-box;
}

.col__short__short:hover {
    cursor: pointer;
    background-color: lightgray;
}


.row__short {
    width: 36vw;
    height: 30vh;
    overflow: hidden;
    border-style: solid;
    box-sizing: border-box;
}
.row__short__nohover {
    width: 36vw;
    height: 30vh;
    overflow: hidden;
    border-style: solid;
    box-sizing: border-box;
}

.row__short:hover {
    background-color: lightgray;
    cursor: pointer;
}


.row__long {
    width: 90vw;
    height: 30vh;
    overflow: hidden;
    border-style: solid;
    box-sizing: border-box;
}
.row__long__nohover {
    width: 90vw;
    height: 30vh;
    overflow: hidden;
    border-style: solid;
    box-sizing: border-box;
}

.row__long:hover {
    background-color: lightgray;
    cursor: pointer;
}


.col__long__mobile {
    width: 45vw;
    height: 70vh;
    overflow: hidden;
    border-style: solid;
    box-sizing: border-box;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;
}

.col__short__mobile {
    width: 45vw;
    height: 35vh;
    overflow: hidden;
    border-style: solid;
    box-sizing: border-box;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;
}

.col__short__short__mobile {
    width: 45vw;
    height: 35vh;
    overflow: hidden;
    border-style: solid;
    box-sizing: border-box;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;
}

.row__short__mobile {
    width: 30vw;
    height: 30vh;
    overflow: hidden;
    border-style: solid;
    box-sizing: border-box;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;
}

.row__long__mobile {
    width: 75vw;
    height: 30vh;
    overflow: hidden;
    border-style: solid;
    box-sizing: border-box;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;
}


.handle {
    margin-top: 0;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;
    height: 1.75vh;
    font-size: 1.25vh;
    color: lightgray;
    border-radius: 25px;
}

.handle:hover {
    background-color: lightgray;
    cursor: -webkit-grab;
    cursor: grab;
}

.handle:active {
    cursor: -webkit-grabbing;
    cursor: grabbing;
    background-color: lightslategrey;
    color: lightslategray;
}

.loader {
    margin-top: 0;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;
    visibility: hidden;
}

.img__handle {
    margin-top: 0;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;
    height: 1.75vh;
    width: 10vw;
    font-size: 1.25vh;
    color: lightgray;
    border-radius: 25px;
}

.img__handle:hover {
    background-color: lightgray;
    cursor: -webkit-grab;
    cursor: grab;
}

.img__handle:active {
    cursor: -webkit-grabbing;
    cursor: grabbing;
    background-color: lightslategrey;
    color: lightslategray;
}

.image {
    width: 8vw;
    height: 11vh;
    min-width: 100px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
}

.img__button {
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    transition-duration: 0.4s;
}

.img__button__search {
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    transition-duration: 0.4s;
}

.img__button__search:hover {
    width: 100%;
    height: 100%;
    cursor: pointer;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    transition-duration: 0.4s;
}

.img__button:hover {
    cursor: pointer;
}

body {
    margin: 0;
  }
.grid {
    display: grid;
    grid-template-columns: 1fr;
    display: inline-block;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

